.profile {
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
    align-items: center;
    width: 68.90625%;
    padding: 40px 0 51px;
    margin: 0;
}

@media all and  (max-width: 850px) {
    .profile {
        display: flex;
        justify-content: center;
        width: 88.125%;
        padding: 40px 0 36px;
    }
}
