.profile__info_about {
    font-weight: normal;
    font-size: 18px;
    line-height: 22px;
    margin: 0;
    padding: 0;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

@media all and  (max-width: 600px) {
    .profile__info_about {
        font-size: 14px;
        line-height: 17px;
        grid-column: 1 / 4;
    }
}
